* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: #000;
  font-family: Courier New, monospace;
  overflow: hidden;
}

#canvas-container {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#ui-container {
  z-index: 2;
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#title {
  display: none;
}

#message-form {
  pointer-events: all;
  opacity: 1;
  background: #001428e6;
  border: 2px solid #0ff;
  border-radius: 15px;
  width: 450px;
  max-width: 450px;
  padding: 30px;
  transition: opacity .3s, transform .3s;
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%)translateY(0);
  box-shadow: 0 0 30px #00ffff80;
}

#message-form.hidden {
  opacity: 0;
  pointer-events: none;
  display: none;
  transform: translateX(-50%)translateY(20px);
}

.close-btn {
  color: #0ff;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 0;
  font-size: 24px;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-btn:hover {
  color: #fff;
  transform: rotate(90deg);
}

#add-wish-btn {
  color: #000;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  pointer-events: all;
  opacity: 0;
  pointer-events: none;
  background: linear-gradient(45deg, #0ff, #08f);
  border: none;
  border-radius: 8px;
  padding: 15px 30px;
  font-family: Courier New, monospace;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
}

#add-wish-btn.visible {
  opacity: 1;
  pointer-events: all;
}

#add-wish-btn:hover {
  transform: translateX(-50%)scale(1.05);
  box-shadow: 0 0 30px #0ff;
}

#message-form h2 {
  color: #0ff;
  text-align: center;
  text-shadow: 0 0 10px #0ff;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  color: #0ff;
  margin-bottom: 5px;
  font-size: 14px;
  display: block;
}

.form-group input, .form-group select {
  color: #fff;
  background: #0009;
  border: 1px solid #0ff;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-family: Courier New, monospace;
}

.form-group input:focus, .form-group select:focus {
  outline: none;
  box-shadow: 0 0 10px #0ff;
}

#mood-buttons {
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  display: grid;
}

.mood-btn {
  color: #fff;
  cursor: pointer;
  pointer-events: all;
  background: #0009;
  border: 2px solid;
  border-radius: 8px;
  padding: 12px;
  font-family: Courier New, monospace;
  font-size: 14px;
  transition: all .3s;
}

.mood-btn.hope {
  border-color: #0ff;
}

.mood-btn.love {
  border-color: #f0f;
}

.mood-btn.dream {
  border-color: #ff0;
}

.mood-btn.gratitude {
  border-color: #0f0;
}

.mood-btn:hover {
  transform: scale(1.05);
}

.mood-btn.active {
  box-shadow: 0 0 20px;
}

.mood-btn.hope.active {
  box-shadow: 0 0 20px #0ff;
}

.mood-btn.love.active {
  box-shadow: 0 0 20px #f0f;
}

.mood-btn.dream.active {
  box-shadow: 0 0 20px #ff0;
}

.mood-btn.gratitude.active {
  box-shadow: 0 0 20px #0f0;
}

#send-btn {
  color: #000;
  cursor: pointer;
  pointer-events: all;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: linear-gradient(45deg, #0ff, #08f);
  border: none;
  border-radius: 8px;
  width: 100%;
  margin-top: 20px;
  padding: 15px;
  font-family: Courier New, monospace;
  font-size: 16px;
  font-weight: bold;
  transition: all .3s;
}

#send-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 0 30px #0ff;
}

#filter-container {
  pointer-events: all;
  align-items: flex-end;
  gap: 10px;
  width: auto;
  max-width: 90%;
  display: flex;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

#filter-wrapper {
  flex-direction: column;
  justify-content: flex-end;
  height: 48px;
  display: flex;
}

#filter-container label {
  color: #0ff;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  white-space: nowrap;
  margin-bottom: 5px;
  font-size: 11px;
  display: block;
}

#country-filter {
  color: #0ff;
  cursor: pointer;
  text-align: left;
  background: #001428f2 url("data:image/svg+xml;utf8,<svg fill='%2300ffff' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'><path d='M7 10l5 5 5-5z'/></svg>") right 12px center / 14px no-repeat;
  border: 2px solid #0ff;
  border-radius: 8px;
  width: fit-content;
  max-width: 200px;
  height: 48px;
  padding: 0 10px 0 20px;
  font-family: Courier New, monospace;
  font-size: 15px;
  box-shadow: 0 0 20px #00ffff4d;
}

#country-filter:focus {
  outline: none;
  box-shadow: 0 0 30px #0ff9;
}

#country-filter {
  appearance: none;
}

#filter-wrapper {
  position: relative;
}

#country-filter:focus {
  outline: none;
}

#prediction-btn {
  color: #f0f;
  cursor: pointer;
  white-space: nowrap;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow: 0 0 15px #f0f;
  background: none;
  border: 2px solid #f0f;
  border-radius: 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  height: 48px;
  padding: 12px 20px;
  font-family: Courier New, monospace;
  font-size: 12px;
  transition: all .3s;
  display: flex;
  box-shadow: 0 0 20px #f0fc;
}

#prediction-btn:hover {
  color: #f8f;
  text-shadow: 0 0 20px #f8f;
  background: #ff00ff26;
  border-color: #f8f;
  transform: scale(1.05);
  box-shadow: 0 0 30px #f0f;
}

#wish-counter {
  color: #0ff;
  text-align: center;
  white-space: nowrap;
  background: #0064644d;
  border: 2px solid #0ff;
  border-radius: 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  height: 48px;
  padding: 12px 20px;
  font-family: Courier New, monospace;
  font-size: 14px;
  font-weight: bold;
  display: flex;
  box-shadow: 0 0 15px #00ffff4d;
}

.char-counter {
  text-align: right;
  color: #0ff;
  margin-top: 5px;
  font-size: 12px;
}

.message-tooltip {
  pointer-events: none;
  z-index: 1000;
  background: #000000f2;
  border: 2px solid;
  border-radius: 8px;
  max-width: 300px;
  padding: 15px;
  font-size: 14px;
  position: fixed;
  box-shadow: 0 0 20px #00ffff80;
}

@media (width <= 768px) {
  #filter-container {
    gap: 8px;
    max-width: 95%;
  }

  #prediction-btn {
    height: 44px;
    padding: 10px 15px;
    font-size: 11px;
    box-shadow: 0 0 20px #f0fc;
  }

  #wish-counter {
    height: 44px;
    padding: 10px 15px;
    font-size: 13px;
  }

  #country-filter {
    height: 48px;
    padding: 10px 20px 10px 10px;
    font-size: 12px;
  }

  #filter-container label {
    margin-bottom: 3px;
    font-size: 9px;
  }

  #message-form {
    min-width: unset;
    width: 90vw;
    padding: 20px;
    bottom: 20px;
  }

  #add-wish-btn {
    padding: 12px 24px;
    font-size: 14px;
    bottom: 20px;
  }

  .mood-btn {
    padding: 10px;
    font-size: 12px;
  }

  #send-btn {
    padding: 12px;
    font-size: 14px;
  }

  .message-tooltip {
    max-width: 200px;
    padding: 12px;
    font-size: 12px;
  }
}

@media (width <= 380px) {
  #filter-container {
    gap: 5px;
    max-width: 98%;
  }

  #prediction-btn {
    letter-spacing: .5px;
    min-width: 80px;
    height: 38px;
    padding: 8px 10px;
    font-size: 9px;
  }

  #wish-counter {
    min-width: 60px;
    height: 38px;
    padding: 8px 10px;
    font-size: 11px;
  }

  #country-filter {
    height: 38px;
    padding: 8px 10px;
    font-size: 10px;
  }

  #filter-container label {
    margin-bottom: 2px;
    font-size: 8px;
  }
}

@media (width >= 381px) and (width <= 430px) {
  #prediction-btn {
    padding: 9px 13px;
    font-size: 10px;
  }

  #wish-counter {
    padding: 9px 13px;
    font-size: 12px;
  }

  #country-filter {
    font-size: 11px;
  }
}

@media (width <= 480px) {
  #message-form {
    width: 95vw;
    height: 40px;
    padding: 15px;
  }

  #mood-buttons {
    grid-template-columns: 1fr;
  }
}

#country-filter {
  height: 40px;
  padding: 8px 12px;
  font-size: 11px;
}

#filter-container label {
  margin-bottom: 2px;
  font-size: 8px;
}

#message-form {
  min-width: unset;
  width: 90vw;
  padding: 20px;
  bottom: 20px;
}

#add-wish-btn {
  padding: 12px 24px;
  font-size: 14px;
  bottom: 20px;
}

.mood-btn {
  padding: 10px;
  font-size: 12px;
}

#send-btn {
  padding: 12px;
  font-size: 14px;
}

.message-tooltip {
  max-width: 200px;
  padding: 12px;
  font-size: 12px;
}

@media (width <= 480px) {
  #prediction-btn {
    padding: 8px 10px;
    font-size: 16px;
  }

  #wish-counter {
    padding: 8px 10px;
    font-size: 11px;
  }

  #message-form {
    width: 95vw;
    padding: 15px;
  }

  #mood-buttons {
    grid-template-columns: 1fr;
  }
}

@media (width <= 520px) {
  :root {
    --ui-scale: clamp(.85, 100vw / 420, 1);
  }

  body {
    overflow-x: hidden;
  }

  #ui-container {
    transform: scale(var(--ui-scale));
    transform-origin: top;
  }

  #filter-container {
    flex-wrap: wrap;
    justify-content: center;
    gap: .6rem;
    width: 96vw;
    left: 50%;
    transform: translateX(-50%);
  }

  #message-form {
    width: 95vw;
    bottom: env(safe-area-inset-bottom, 12px);
  }

  #add-wish-btn {
    bottom: env(safe-area-inset-bottom, 12px);
  }

  canvas {
    width: 100vw !important;
    height: 100vh !important;
  }
}
