* {
            margin: 0;
            padding: 0;
            box-sizing: border-box;
        }

        body {
            font-family: 'Courier New', monospace;
            overflow: hidden;
            background: #000;
            color: #fff;
        }

        #canvas-container {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
        }

        #ui-container {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            pointer-events: none;
        }

        #title {
            display: none;
        }

        #message-form {
            position: absolute;
            bottom: 60px;
            left: 50%;
            transform: translateX(-50%);
            background: rgba(0, 20, 40, 0.9);
            padding: 30px;
            border-radius: 15px;
            border: 2px solid #00ffff;
            box-shadow: 0 0 30px rgba(0, 255, 255, 0.5);
            pointer-events: all;
            width: 450px;
            max-width: 450px;
            transition: opacity 0.3s, transform 0.3s;
            opacity: 1;
            transform: translateX(-50%) translateY(0);
        }

        #message-form.hidden {
            opacity: 0;
            pointer-events: none;
            transform: translateX(-50%) translateY(20px);
            display: none;
        }

        .close-btn {
            position: absolute;
            top: 15px;
            right: 15px;
            background: none;
            border: none;
            color: #00ffff;
            font-size: 24px;
            cursor: pointer;
            padding: 0;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s;
        }

        .close-btn:hover {
            color: #fff;
            transform: rotate(90deg);
        }

        #add-wish-btn {
            position: absolute;
            bottom: 60px;
            left: 50%;
            transform: translateX(-50%);
            padding: 15px 30px;
            background: linear-gradient(45deg, #00ffff, #0088ff);
            border: none;
            border-radius: 8px;
            color: #000;
            font-size: 16px;
            font-weight: bold;
            cursor: pointer;
            font-family: 'Courier New', monospace;
            text-transform: uppercase;
            letter-spacing: 2px;
            pointer-events: all;
            transition: all 0.3s;
            opacity: 0;
            pointer-events: none;
        }

        #add-wish-btn.visible {
            opacity: 1;
            pointer-events: all;
        }

        #add-wish-btn:hover {
            transform: translateX(-50%) scale(1.05);
            box-shadow: 0 0 30px #00ffff;
        }

        #message-form h2 {
            color: #00ffff;
            margin-bottom: 20px;
            text-align: center;
            text-shadow: 0 0 10px #00ffff;
        }

        .form-group {
            margin-bottom: 15px;
        }

        .form-group label {
            display: block;
            margin-bottom: 5px;
            color: #00ffff;
            font-size: 14px;
        }

        .form-group input,
        .form-group select {
            width: 100%;
            padding: 10px;
            background: rgba(0, 0, 0, 0.6);
            border: 1px solid #00ffff;
            border-radius: 5px;
            color: #fff;
            font-family: 'Courier New', monospace;
        }

        .form-group input:focus,
        .form-group select:focus {
            outline: none;
            box-shadow: 0 0 10px #00ffff;
        }

        #mood-buttons {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 10px;
        }

        .mood-btn {
            padding: 12px;
            background: rgba(0, 0, 0, 0.6);
            border: 2px solid;
            border-radius: 8px;
            color: #fff;
            cursor: pointer;
            transition: all 0.3s;
            font-family: 'Courier New', monospace;
            font-size: 14px;
            pointer-events: all;
        }

        .mood-btn.hope {
            border-color: #00ffff;
        }

        .mood-btn.love {
            border-color: #ff00ff;
        }

        .mood-btn.dream {
            border-color: #ffff00;
        }

        .mood-btn.gratitude {
            border-color: #00ff00;
        }

        .mood-btn:hover {
            transform: scale(1.05);
        }

        .mood-btn.active {
            box-shadow: 0 0 20px currentColor;
        }

        .mood-btn.hope.active {
            box-shadow: 0 0 20px #00ffff;
        }

        .mood-btn.love.active {
            box-shadow: 0 0 20px #ff00ff;
        }

        .mood-btn.dream.active {
            box-shadow: 0 0 20px #ffff00;
        }

        .mood-btn.gratitude.active {
            box-shadow: 0 0 20px #00ff00;
        }

        #send-btn {
            width: 100%;
            padding: 15px;
            background: linear-gradient(45deg, #00ffff, #0088ff);
            border: none;
            border-radius: 8px;
            color: #000;
            font-size: 16px;
            font-weight: bold;
            cursor: pointer;
            margin-top: 20px;
            transition: all 0.3s;
            pointer-events: all;
            font-family: 'Courier New', monospace;
            text-transform: uppercase;
            letter-spacing: 2px;
        }

        #send-btn:hover {
            transform: scale(1.05);
            box-shadow: 0 0 30px #00ffff;
        }

        #filter-container {
            position: absolute;
            top: 20px;
            left: 50%;
            transform: translateX(-50%);
            pointer-events: all;
            width: auto;
            max-width: 90%;
            display: flex;
            align-items: flex-end;
            gap: 10px;
        }

        #filter-wrapper {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            height: 48px;
        }

        #filter-container label {
            display: block;
            color: #00ffff;
            font-size: 11px;
            margin-bottom: 5px;
            text-transform: uppercase;
            letter-spacing: 1px;
            text-align: center;
            white-space: nowrap;
        }

        #country-filter {
            height: 48px;
            padding: 0 40px 0 20px;
            padding-right: 10px;
            background: rgba(0, 20, 40, 0.95);
            border: 2px solid #00ffff;
            border-radius: 8px;
            color: #00ffff;
            font-family: 'Courier New', monospace;
            cursor: pointer;
            font-size: 15px;
            text-align: left;
            box-shadow: 0 0 20px rgba(0, 255, 255, 0.3);
            width: fit-content;
            max-width: 200px;
            background-image: url("data:image/svg+xml;utf8,<svg fill='%2300ffff' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'><path d='M7 10l5 5 5-5z'/></svg>");
            background-repeat: no-repeat;
            background-position: right 12px center;
            background-size: 14px;
        }

        #country-filter:focus {
            outline: none;
            box-shadow: 0 0 30px rgba(0, 255, 255, 0.6);
        }
        
        /* ===== CUSTOM SELECT ARROW FIX ака Залупа Лягушки ===== */
        
        #filter-wrapper {
  position: relative;
}

#country-filter {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

#filter-wrapper {
  position: relative;
}

#country-filter:focus {
  outline: none;
}


        #prediction-btn {
            padding: 12px 20px;
            background: transparent;
            border: 2px solid #ff00ff;
            border-radius: 8px;
            color: #ff00ff;
            font-family: 'Courier New', monospace;
            cursor: pointer;
            font-size: 12px;
            transition: all 0.3s;
            box-shadow: 0 0 20px rgba(255, 0, 255, 0.8);
            white-space: nowrap;
            flex-shrink: 0;
            height: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
            text-transform: uppercase;
            letter-spacing: 1px;
            text-shadow: 0 0 15px rgba(255, 0, 255, 1);
        }

        #prediction-btn:hover {
            background: rgba(255, 0, 255, 0.15);
            box-shadow: 0 0 30px rgba(255, 0, 255, 1);
            color: #ff88ff;
            text-shadow: 0 0 20px rgba(255, 136, 255, 1);
            border-color: #ff88ff;
            transform: scale(1.05);
        }

        #wish-counter {
            padding: 12px 20px;
            background: rgba(0, 100, 100, 0.3);
            border: 2px solid #00ffff;
            border-radius: 8px;
            color: #00ffff;
            font-family: 'Courier New', monospace;
            font-size: 14px;
            font-weight: bold;
            text-align: center;
            box-shadow: 0 0 15px rgba(0, 255, 255, 0.3);
            white-space: nowrap;
            flex-shrink: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 48px;
        }

        .char-counter {
            text-align: right;
            font-size: 12px;
            color: #00ffff;
            margin-top: 5px;
        }

        .message-tooltip {
            position: fixed;
            background: rgba(0, 0, 0, 0.95);
            padding: 15px;
            border-radius: 8px;
            border: 2px solid;
            pointer-events: none;
            font-size: 14px;
            max-width: 300px;
            z-index: 1000;
            box-shadow: 0 0 20px rgba(0, 255, 255, 0.5);
        }

        /* MOBILE OPTIMIZATIONS */
        @media (max-width: 768px) {
            #filter-container {
                gap: 8px;
                max-width: 95%;
            }

            #prediction-btn {
                font-size: 11px;
                padding: 10px 15px;
                height: 44px;
                box-shadow: 0 0 20px rgba(255, 0, 255, 0.8);
            }

            #wish-counter {
                font-size: 13px;
                padding: 10px 15px;
                height: 44px;
            }

            #country-filter {
                font-size: 12px;
                padding: 10px 10px;
                padding-right: 20px;
                height: 48px;
            }

            #filter-container label {
                font-size: 9px;
                margin-bottom: 3px;
            }

            #message-form {
                min-width: unset;
                width: 90vw;
                padding: 20px;
                bottom: 20px;
            }

            #add-wish-btn {
                bottom: 20px;
                font-size: 14px;
                padding: 12px 24px;
            }

            .mood-btn {
                font-size: 12px;
                padding: 10px;
            }

            #send-btn {
                font-size: 14px;
                padding: 12px;
            }

            .message-tooltip {
                font-size: 12px;
                padding: 12px;
                max-width: 200px;
            }
        }

        /* Small phones (iPhone SE, 12/13 mini) - до 380px */
        @media (max-width: 380px) {
            #filter-container {
                gap: 5px;
                max-width: 98%;
            }

            #prediction-btn {
                font-size: 9px;
                padding: 8px 10px;
                height: 38px;
                min-width: 80px;
                letter-spacing: 0.5px;
            }

            #wish-counter {
                font-size: 11px;
                padding: 8px 10px;
                height: 38px;
                min-width: 60px;
            }

            #country-filter {
                font-size: 10px;
                padding: 8px 10px;
                height: 38px;
            }

            #filter-container label {
                font-size: 8px;
                margin-bottom: 2px;
            }
        }

        /* Medium phones (iPhone 12/13/14 standard) - 381-430px */
        @media (min-width: 381px) and (max-width: 430px) {
            #prediction-btn {
                font-size: 10px;
                padding: 9px 13px;
            }

            #wish-counter {
                font-size: 12px;
                padding: 9px 13px;
            }

            #country-filter {
                font-size: 11px;
            }
        }

        @media (max-width: 480px) {
            #message-form {
                width: 95vw;
                padding: 15px;
                height: 40px;
            }

            #mood-buttons {
                grid-template-columns: 1fr;
            }
        }

            #country-filter {
                font-size: 11px;
                padding: 8px 12px;
                height: 40px;
            }

            #filter-container label {
                font-size: 8px;
                margin-bottom: 2px;
            }

            #message-form {
                min-width: unset;
                width: 90vw;
                padding: 20px;
                bottom: 20px;
            }

            #add-wish-btn {
                bottom: 20px;
                font-size: 14px;
                padding: 12px 24px;
            }

            .mood-btn {
                font-size: 12px;
                padding: 10px;
            }

            #send-btn {
                font-size: 14px;
                padding: 12px;
            }

            .message-tooltip {
                font-size: 12px;
                padding: 12px;
                max-width: 200px;
            }
        

        @media (max-width: 480px) {
            #prediction-btn {
                font-size: 16px;
                padding: 8px 10px;
            }

            #wish-counter {
                font-size: 11px;
                padding: 8px 10px;
            }

            #message-form {
                width: 95vw;
                padding: 15px;
            }

            #mood-buttons {
                grid-template-columns: 1fr;
            }
        }
    
/* ===== UNIVERSAL MOBILE LAYOUT ENGINE ===== */
@media (max-width: 520px) {
  :root { --ui-scale: clamp(0.85, 100vw / 420, 1); }
  body { overflow-x: hidden; }
  #ui-container { transform: scale(var(--ui-scale)); transform-origin: top center; }
  #filter-container { width:96vw; left:50%; transform:translateX(-50%); flex-wrap:wrap; justify-content:center; gap:.6rem; }
  #message-form { width:95vw; bottom: env(safe-area-inset-bottom, 12px); }
  #add-wish-btn { bottom: env(safe-area-inset-bottom, 12px); }
  canvas { width:100vw!important; height:100vh!important; }
}

